<script>
	import { Graphic, Line, PointLayer, XAxis, YAxis } from '@snlab/florence'

	let x = [1];
	let y = [1];

	let summands = ['1/1'];
	let posTermPosition = 1;
	let negTermPosition = 1;

	const addPositiveTerm = () => {
		posTermPosition += 1;
		const k = 2*posTermPosition - 1;
		summands += ` + 1/${k}`;
		x = [...x, posTermPosition + negTermPosition];
		y = [...y, y[y.length-1] + 1/k];
	}

	const addNegativeTerm = () => {
		negTermPosition += 1;
		const k = 2*negTermPosition-2;
		summands += ` - 1/${k}`;
		x = [...x, posTermPosition + negTermPosition];
		y = [...y, y[y.length-1] - 1/k];
	}

	const handle100Clicks = () => {
		for (let i = 0; i < 100; i++) {
			addPositiveTerm();
		}
	}

	const reset = () => {
		x = [1];
		y = [1];

		summands = ['1/1'];
		posTermPosition = 1;
		negTermPosition = 1;
	}

</script>

<h1>Conditional convergence of the harmonic series</h1>

<div>
	<Graphic
		width={400}
		height={200}
		scaleX={[0, 1.05*Math.max(20, x[x.length - 1])]}
		scaleY={[0, 1.05*Math.max(2, ...y)]}
		flipY
		padding={20}
	>

		{#if (x.length > 1)}
			<Line 
				{x}
				{y}
				stroke={'steelblue'}
				strokeWidth={1}
			/>

			<PointLayer 
				{x}
				{y}
				stroke={'steelblue'}
				radius={2}
				strokeWidth={1}
				fill='white'
		/>
		{/if}

		<XAxis />
		<YAxis />

	</Graphic>
</div>

<div class="button-panel">
	<button
		on:click={addPositiveTerm}>
		One more positive term
	</button>
	<button
		on:click={addNegativeTerm}>
		One more negative term
	</button>
	<button
		on:click={handle100Clicks}>
		100 more positive terms
	</button>
	<button
	on:click={reset}>
	Reset
</button>
</div>

{ summands }

<style>
	div { 
		display: block;
		margin-left: auto; 
		margin-right: auto;
		text-align: center;
	}

	.button-panel {
		margin-top: 20px;
		margin-bottom: 20px;
	}
</style>